import React from 'react';
import Aux from '../../../hoc/Aux/Aux';
import Button from '../../UI/Button/Button';


const orderSummary = (props) =>{
    const ingredientSummary =Object.keys(props.ingredients)
        .map(igKey =>{
            return <li key={igKey}><span style={{textTransform: 'capitalize'}}>{igKey}: {props.ingredients[igKey]}</span></li>

        });

    return(
       <Aux>
            <h3>Your Order</h3>
           <p>Adelicious burger with the following ingredients:</p>
           <ul>
               {ingredientSummary}
           </ul>
           <p><strong>Total Price: {props.price.toFixed(2)}</strong></p>
            <p>Continue Checkout?</p>
           <Button Btntype="Success" clicked={props.continued}>OK </Button>
           <Button Btntype="Danger" clicked={props.canceled}>Cancel </Button>

       </Aux>
    );

};


export default orderSummary;